import Vue from 'vue';
import i18n from '../../../i18n';
import * as XDraggable from 'vuedraggable';
import { v4 as uuid } from 'uuid';
export default Vue.extend({
    i18n: i18n('mobile/views/pages/widgets.vue'),
    components: {
        XDraggable
    },
    data() {
        return {
            showNav: false,
            customizing: false,
            widgetAdderSelected: null
        };
    },
    computed: {
        widgets() {
            return this.$store.getters.mobileHome || [];
        }
    },
    created() {
        if (this.widgets.length == 0) {
            this.$store.commit('setMobileHome', [{
                    name: 'calendar',
                    id: 'a', data: {}
                }, {
                    name: 'activity',
                    id: 'b', data: {}
                }, {
                    name: 'rss',
                    id: 'c', data: {}
                }, {
                    name: 'photo-stream',
                    id: 'd', data: {}
                }, {
                    name: 'nav',
                    id: 'f', data: {}
                }, {
                    name: 'version',
                    id: 'g', data: {}
                }]);
        }
    },
    mounted() {
        document.title = this.$root.instanceName;
    },
    methods: {
        hint() {
            this.$root.dialog({
                type: 'info',
                text: this.$t('widgets-hints')
            });
        },
        widgetFunc(id) {
            const w = this.$refs[id][0];
            if (w.func)
                w.func();
        },
        onWidgetSort() {
            this.saveHome();
        },
        addWidget() {
            if (this.widgetAdderSelected == null)
                return;
            this.$store.commit('addMobileHomeWidget', {
                name: this.widgetAdderSelected,
                id: uuid(),
                data: {}
            });
        },
        removeWidget(widget) {
            this.$store.commit('removeMobileHomeWidget', widget);
        },
        saveHome() {
            this.$store.commit('setMobileHome', this.widgets);
        }
    }
});
